import { Button } from "@chakra-ui/react"
import { emit } from "process"
import { Fragment, useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { API } from "../API/API"
import useSignInForm from "../component/SignInForm"
import { CONFIG } from "../config/config"
import { isFail, isSuccess } from "../model/Result"

const SignInLayout: React.FC<{}> = () => {
    const navi = useNavigate()


    useEffect(() => {
        (async () => {
            const isLogin = await API.isLogin()
            if (isLogin)
                navi(`/`)
        })()
    })

    const [formInstance, opt] = useSignInForm({
        async doLogin({ email, passwd, rememberMe }) {
            const res = await API.login(email, passwd, rememberMe)
            if (isFail(res)) {
                alert('登陆失败！')
                return
            }
            navi(`/`)
        },
        onCreateBtnClick() {
            navi(`/signup`)
        },
    })

    return (
        <Fragment>
            {formInstance}
        </Fragment>
    )
}
export default SignInLayout
