import { AddIcon, CheckCircleIcon, DownloadIcon } from "@chakra-ui/icons";
import { Box, Checkbox, Flex, HStack, IconButton, Input, Textarea, useBoolean, useCheckbox, VStack } from "@chakra-ui/react";
import { Fragment, useEffect, useState } from "react";
import { API } from "../API/API";
import { isFail } from "../model/Result";
import { Task } from "../model/Task";
import * as R from 'ramda'
import TodoList from "../component/TodoList";
import { useIfLogin } from "../hook/useIfLogin";
import { CONFIG } from "../config/config";

function sleep(n: number) {
  return new Promise(resolve => {
    setTimeout(() => resolve(null), n)
  })
}

const Main = () => {
  useIfLogin()

  const [tasks, setTasks] = useState<Task[]>([])
  const [addTaskStr, setAddTaskStr] = useState("")
  const [addTaskBtnLoading, addTaskBtnLoadingOpt] = useBoolean(false)

  useEffect(() => {
    fetchTasks()
  }, [])

  async function fetchTasks() {
    const tasks = await API.getAllTask()
    if(isFail(tasks)) {
      alert('获取失败' + tasks.message)
      return 
    } 
    setTasks(tasks.data)
  }

  async function addTask() {
    try {
      addTaskBtnLoadingOpt.on()
      await API.addTask(addTaskStr)
      await fetchTasks()
    } catch (e) {
      console.error(e)
      alert(e)
    } finally {
      setAddTaskStr('')
      addTaskBtnLoadingOpt.off()
    }
  }

  return (
    <Flex mt={2} wrap="wrap" gap={2} justifyContent='center'>
      <HStack width={"90%"} justifyContent="center" mt={4}>
        <Input flexGrow={1} placeholder="add new task..." minWidth="300px" value={addTaskStr} onChange={e => setAddTaskStr(e.target.value)}/>
        <IconButton isLoading={addTaskBtnLoading} aria-label={""} colorScheme="blue" icon={<AddIcon />} onClick={e => addTask()}/>
      </HStack>
      <TodoList tasks={tasks} onTaskToggleClick={async id => {
        await API.toggleTask(id)
        const tasks = await API.getAllTask()
        if(isFail(tasks)) {
          alert('获取失败' + tasks.message)
          return 
        } 
        setTasks(tasks.data)
      }}/>
    </Flex>
  )
}

export default Main
