export type ValidationResult = [true, null] | [false, string]

/**
 * email validation
 */
export function validEmail(email: string) : ValidationResult  {
    if (email
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      ) !== null) {
        return [true, null]
      } 
      return [false, 'invalid email!']
};

/**
 * password validation.
 * 
 * password is valid **if and only if**:
 * - length is between 6 and 32
 * - must contain both letters and digits
 * @param password 
 * @return `null` if valid else the reason why fail
 */
export function validPassword(password: string) : ValidationResult {
  if (password.length < 6 || password.length > 32) {
    return [false, 'password length must between 6 and 32!']
  }
  if (password.match(/[0-9]/) === null || password.match(/[a-zA-Z]/) === null) {
    return [false, 'password must contain both letters and digits']
  }
  return [true, null]
}

/**
 * verify code validation, length must be 7
 */
export function validVerCode(verCode: string) : ValidationResult {
    if (verCode === '')
        return [false, 'verify code can\'t be empty!']
    if (verCode.length !== 7) 
        return [false, 'verify code\'s length must be 7!']
    return [true, null]

}