
export type Result<T> = Success<T> | Fail
export type Success<T> = {
    code: 'OK',
    message: string,
    data: T 
}
export type Fail = {
    code: 'BAD_REQUEST' | 'INTERNAL_SERVER_ERROR' | 'NOT_LOGIN',
    message: string,
    data: null 
}

export function isSuccess<T>(result: Result<T>) : result is Success<T> {
    return result.code === 'OK'
}

export function isFail<T>(result: Result<T>) : result is Fail {
    return result.code === 'BAD_REQUEST' || 
            result.code === 'INTERNAL_SERVER_ERROR' ||
            result.code ===  'NOT_LOGIN'
}
