/**
 * The Form which Sign up will use, it will be a uncontrolled component which contains it's own state.
 * 
 * and there will be questions such as... how to make operations like clear all input? 
 */
import { Button, FormControl, FormErrorMessage, FormHelperText, FormLabel, Input, StackItem, useBoolean, VStack, Image, Spinner } from "@chakra-ui/react"
import { useEffect, useState } from "react"
import { CONFIG } from "../config/config"
import { useValidationState } from "../hook/useValidationState"
import { ValidationResult, validEmail, validVerCode } from "../util/Validation"

/**
 * caller provided
 */
type SignUpFormProps = {
  doSignUp?: (email: string, verCode: string) => Promise<void>
}

type SignUpFormOpt = {
  clearInput(): void
  getState(): {email: string, verCode: string}
}



export default function useSignUpForm(props: SignUpFormProps = {}): [JSX.Element, SignUpFormOpt] {
  const [email, setEmail, ifEmailValid, emailInvalidReason] = useValidationState<string>('', validEmail)
  const [displayEmailErorr, displayEmailErorrOpt] = useBoolean(false)

  const [verCode, setVerCode, ifVerCodeValid, verCodeInvalidReason] = useValidationState<string>('', validVerCode)
  const [displayVerCodeErorr, displayVerCodeErorrOpt] = useBoolean(false)

  const [signUpBtnLoading, signUpBtnLoadingOpt] = useBoolean(false)
  const [signUpBtnValid, signUpBtnValidOpt] = useBoolean(false)

  const [counter, setCounter] = useState(0)

  function verCodeClick() {
    setCounter(c => c + 1)
    setVerCode('')
    signUpBtnValidOpt.off()
  }

  useEffect(() => {
    displayEmailErorrOpt.off()
  }, [email])

  useEffect(() => {
    displayVerCodeErorrOpt.off()
  }, [verCode])

  function onSignUpBtnClick() {
    displayEmailErorrOpt.on()
    displayVerCodeErorrOpt.on()
    if (!ifEmailValid || !ifVerCodeValid)
      return
    if (props.doSignUp) {
      signUpBtnLoadingOpt.on()
      props.doSignUp(email, verCode)
        .then(() => signUpBtnLoadingOpt.off())
        .finally(() => {
          setVerCode('')
          verCodeClick()
        })
    } 
  }

  return [(
    <VStack>
      <StackItem width={'320px'}>
        <FormControl isInvalid={displayEmailErorr && !ifEmailValid}>
            <FormLabel>Email address</FormLabel>
            <Input type='email' value={email} onChange={e => setEmail(e.target.value)} />
            <FormHelperText>Email is used for specification and authentication,.</FormHelperText>
            <FormErrorMessage>{emailInvalidReason}</FormErrorMessage>
        </FormControl>
      </StackItem>      

      <StackItem width={'320px'} display='flex' justifyContent='space-between' alignItems='center' >
        <FormControl isInvalid={displayVerCodeErorr && !ifVerCodeValid}>
            <FormLabel>Verify Code</FormLabel>
            <Input width="100px" type='input' value={verCode} onChange={e => setVerCode(e.target.value)} />
            <FormErrorMessage>{verCodeInvalidReason}</FormErrorMessage>
        </FormControl>
        <Image fallback={<Spinner size="lg" mr='80px'/>} onClick={e => verCodeClick()} src={CONFIG.BASE_URL + "/captcha" + "?cc=" + counter} onLoad={_ => signUpBtnValidOpt.on()}/>
      </StackItem>

      <StackItem width={'320px'} display='flex' justifyContent='space-between'>
        <Button width='100%' disabled={!signUpBtnValid} colorScheme={'blue'} onClick={onSignUpBtnClick} isLoading={signUpBtnLoading}>
          Sign Up
        </Button>
      </StackItem>
    </VStack>
  ), {
    clearInput() {
      setEmail('')
      setVerCode('')
    },
    getState() {
      return {email, verCode}
    }
  }]
}
