import { useEffect, useState } from "react";
import { ValidationResult } from "../util/Validation";

export function useValidationState<A>(initialValue: A | (() => A), validationFn: (v : A) => ValidationResult): 
    [A, React.Dispatch<React.SetStateAction<A>>, ...ValidationResult] {
    const [value, setValue] = useState(initialValue)
    const [validationResult, setIfValid] = useState<ValidationResult>([true, null])
    useEffect(() => {
        setIfValid(validationFn(value))
    }, [value])
    return [value, setValue, ...validationResult]
}