import { Flex, HStack, Checkbox, Box, Spinner } from "@chakra-ui/react"
import { Set } from "immutable"
import { loadavg } from "os"
import * as R from "ramda"
import { useEffect, useState } from "react"
import { Task } from "../model/Task"

type TodoListProps = {
	tasks?: Task[],
	onTaskToggleClick?: (id: number) => Promise<void>
}

function sleep(n: number) {
  return new Promise(resolve => {
    setTimeout(() => resolve(null), n)
  })
}

const TodoList: React.FC<TodoListProps> = ({
	tasks = [],
	onTaskToggleClick = async (id) => {
		await sleep(100)
	}
}) => {

	const [taskLoadings, setTaskLoadings] = useState<Set<number>>(Set())
	const checkboxClick = (id: number) => () => (async () => {
		setTaskLoadings(loading => loading.add(id))
		await onTaskToggleClick(id)
	})().finally(() => setTaskLoadings(loadavg => loadavg.remove(id)))

	return (
		<Flex mt={2} wrap="wrap" gap={2} justifyContent='center'>
			{tasks.map(({ id, done, endDate, startDate, descriptionTokens, projectTags, contextTags, kvTags }: Task) => (
				<HStack width={"90%"} border={'1px solid'} alignItems='center' gap={2} key={id}>
					{taskLoadings.contains(id) ? <Spinner ml={4} size='md' /> : <Checkbox ml={4} size='lg' colorScheme='green' isChecked={done} onChange={checkboxClick(id)} />}
					<Box minWidth={40} flexGrow={1}>
						<Box>{endDate} - {startDate}</Box>
						<Box>{[...projectTags, ...contextTags,
						R.compose(R.map(k => `${k}:${kvTags[k]}`), R.keys)(kvTags)].join(' ')}</Box>
						<Box>{descriptionTokens.join(' ')}</Box>
					</Box>
				</HStack>
			))}
		</Flex>
	)
}

export default TodoList