import { Fragment } from "react"
import { useNavigate } from "react-router-dom"
import { API } from "../API/API"
import useSignUpForm from "../component/SignupForm"
import { CONFIG } from "../config/config"
import { isFail } from "../model/Result"

const SignUpLayout : React.FC<{}> = () => {
    const navi = useNavigate()
    const [formInstance, opt] = useSignUpForm({
        async doSignUp(email, verCode) {
            const res = await API.signup(email, verCode)
            if (isFail(res)) {
                alert('注册失败，请检查验证码是否正确！')
                return
            }
            alert("注册成功，密码已发送至你的邮箱。")
            navi(`/signin`)
        },
    })
    return (
    <Fragment>
        {formInstance}
    </Fragment>
    )
}
export default SignUpLayout