/**
 * The Form which Sign in will use, it will be a uncontrolled component which contains it's own state.
 * 
 * and there will be questions such as... how to make operations like clear all input? 
 */
import { Button, FormControl, FormErrorMessage, FormLabel, Input, StackItem, Switch, useBoolean, VStack } from "@chakra-ui/react"
import { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import { CONFIG } from "../config/config"
import { useValidationState } from "../hook/useValidationState"
import { ValidationResult, validEmail, validPassword } from "../util/Validation"

type LoginData = {
  email: string,
  passwd: string,
  rememberMe: boolean
}

/**
 * caller provided
 */
type SignInFormProps = {
  doLogin?: (loginData: LoginData) => Promise<void>,
  onCreateBtnClick?: () => void
}

type SignInFormOpt = {
  clearInput(): void
  getState(): LoginData
}

export default function useSignInForm(props: SignInFormProps = {}): [JSX.Element, SignInFormOpt] {
  
  const [email, setEmail, ifEmailValid, emailInvalidReason] = useValidationState<string>('', validEmail)
  const [displayEmailErorr, displayEmailErorrOpt] = useBoolean(false)

  const [passwd, setPasswd, ifPasswdValid, passwdInvalidReason] = useValidationState<string>('', validPassword)
  const [displayPasswdErorr, displayPasswdErorrOpt] = useBoolean(false)

  const [rememberMe, rememberMeOpt] = useBoolean(false)

  const [signInBtnLoading, signInBtnLoadingOpt] = useBoolean(false)

  useEffect(() => {
    displayEmailErorrOpt.off()
  }, [email])

  useEffect(() => {
    displayPasswdErorrOpt.off()
  }, [passwd])

  function onSignInBtnClick() {
    displayEmailErorrOpt.on()
    displayPasswdErorrOpt.on()
    if (!ifEmailValid || !ifPasswdValid)
      return
    if (props.doLogin) {
      signInBtnLoadingOpt.on()
      props.doLogin({email, passwd, rememberMe})
        .then(() => {
          signInBtnLoadingOpt.off()
        })
    } 
  }

  return [(
    <VStack>
      <StackItem minWidth={'320px'}>
        <FormControl isInvalid={displayEmailErorr && !ifEmailValid}>
            <FormLabel>Email address</FormLabel>
            <Input type='email' value={email} onChange={e => setEmail(e.target.value)} />
            <FormErrorMessage>{emailInvalidReason}</FormErrorMessage>
        </FormControl>
      </StackItem>      
      <StackItem minWidth={'320px'}>
        <FormControl isInvalid={displayPasswdErorr && !ifPasswdValid}>
          <FormLabel>password</FormLabel>
          <Input type='password' value={passwd} onChange={e => setPasswd(e.target.value)} />
          <FormErrorMessage>{passwdInvalidReason}</FormErrorMessage>
        </FormControl>
      </StackItem>

      <StackItem minWidth={'320px'} display='flex'>
      <FormControl marginTop={1} marginBottom={2} display='flex' alignItems='center'  justifyContent='flex-end'>
        <FormLabel mb={0}>
          Remember Me
        </FormLabel>
        <Switch isChecked={rememberMe} onChange={e => rememberMeOpt.toggle()} />
      </FormControl>
      </StackItem>
      <StackItem minWidth={'320px'}>
      <Button width={"100%"} colorScheme={'blue'} onClick={onSignInBtnClick} isLoading={signInBtnLoading}>
          Sign In
        </Button>
      </StackItem>
      <StackItem minWidth={'320px'}>
      <Button  width={"100%"}  onClick={e => props.onCreateBtnClick?.apply(null) }>Create new Account</Button>
      </StackItem>
    </VStack>
  ), {
    clearInput() {
      setEmail('')
      setPasswd('')
      rememberMeOpt.off()
    },
    getState() {
      return { email, passwd, rememberMe }
    }
  }]
}
